.navbar {
    transition: 4s ease-in-out all;
}

.navbar-principal {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 15px;
    padding-right: 15px;
    height: 50px;
    background-color: #303030;
    transition: 4s ease-in-out display;
}

.navbar .navbar-logo {
    font-size: 35px;
    font-weight: 600;
    color: rgb(250, 0, 207);
    width: 40%;
    position: relative;
    top: 0px;
    font-family: cursive;
    font-style: oblique;
}

.navbar .navbar-logo span {
    color: rgb(250, 200, 0);
}

.navbar-pages {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
}

.navbar-pages .link {
    font-size: 13px;
    font-weight: 700;
    text-decoration: none;
    color: #ddd;
    position: relative;
    text-transform: uppercase;
    user-select: none;
}

.navbar-pages .link.active,
.navbar-additional .link.active {
    color: rgb(235, 25, 200);
    /* font-size: 18px; */
    transform: scale(1.2);
}

.navbar-search {
    width: 100%;
    position: relative;
    left: 0;
    animation: showNavbar 1s ease-in-out 1;
}

@keyframes showNavbar {
    0% {
        left: -100%;
    }
    100% {
        left: 0;
    }
}

.navbar-additional {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: max-content;
}

.link.icon.icon-search {
    cursor: pointer;
}

.navbar-additional .link.icon {
    position: relative;
}

.shopping-cart-count {
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: rgb(221, 7, 7);
    color: #ddd;
    text-align: center;
    top: 4px;
    right: 4px;
    position: absolute;
    text-decoration: none;
    cursor: pointer;
}

.navbar-additional .link.icon svg {
    width: 30px;
    height: 30px;
    color: #ddd;
}

@media screen and (max-width:768px) {
    .navbar-principal {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 20px;
        padding-right: 15px;
        padding-bottom: 25px;
        background-color: #303030;
        height: auto;
    }
    .navbar .navbar-logo {
        left: 10px;
    }
    .navbar-pages {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-start;
        gap: 20px;
        width: auto;
    }
    .navbar-additional {
        gap: 15px;
    }
    .shopping-cart-count {
        top: -18px;
        right: 30px;
        position: relative;
    }
}

